<template>
    <div>
        <ul>
            <li v-for="item in files" :key="item.imgPath">
                <div v-if="matchType(item.imgPath) == 'image'">
                    <img :src="item.imgPath" alt="files" style="width:100%" />
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import axios from 'axios'
// axios.defaults.baseURL = '/api'
export default {
    data() {
        return {
            files: []
        }
    },
    async beforeRouteEnter(to, from, next) {
        console.log(to.params.pageId)
        const pageId = to.params.pageId
        next(vm => {
            vm.getFiles(pageId)
        })
    },
    // mounted: function() {
    //     this.getFiles(this.pageId)
    // },
    methods: {
        getFiles(pageId) {
            axios
                .get('/tenantStaticPage/getPicList?pageId=' + pageId, {
                    responseType: 'json'
                })
                .then(response => {
                    this.files = response.data.data.map(item => {
                        return {
                            imgPath: item.path
                        }
                    })
                })
        },
        matchType(fileName) {
            // 
            var suffix = ''
            // 
            var result = ''
            try {
                var flieArr = fileName.split('.')
                suffix = flieArr[flieArr.length - 1]
            } catch (err) {
                suffix = ''
            }
            // fileName false
            if (!suffix) {
                result = false
                return result
            }
            // 
            var imglist = ['png', 'jpg', 'jpeg', 'bmp', 'gif']
            // 
            result = imglist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'image'
                return result
            }
            // txt
            var txtlist = ['txt']
            result = txtlist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'txt'
                return result
            }
            //  excel
            var excelist = ['xls', 'xlsx']
            result = excelist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'excel'
                return result
            }
            //  word
            var wordlist = ['doc', 'docx']
            result = wordlist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'word'
                return result
            }
            //  pdf
            var pdflist = ['pdf']
            result = pdflist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'pdf'
                return result
            }
            //  ppt
            var pptlist = ['ppt']
            result = pptlist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'ppt'
                return result
            }
            //  
            var videolist = ['mp4', 'm2v', 'mkv']
            result = videolist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'video'
                return result
            }
            //  
            var radiolist = ['mp3', 'wav', 'wmv']
            result = radiolist.some(function(item) {
                return item == suffix
            })
            if (result) {
                result = 'radio'
                return result
            }
            //  
            result = 'other'
            return result
        }
    }
}
</script>
